@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-extra-bold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-semi-bold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.container {
  display: block;
  width: 290px;
  padding: 0 15px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 678px;
    padding: 0 45px;
  }
}
@media (min-width: 1366px) {
  .container {
    width: 1160px;
    padding: 0 103px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  background-color: #ffffff;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  line-height: 22px;
  font-weight: 300;
  color: #102654;
  padding: 0;
  margin: 0;
  min-width: 320px;
}

.hero {
  background: linear-gradient(90deg, #1c3477 0%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
  color: #ffffff;
  padding: 0;
  margin: 0;
  padding-bottom: 32px;
}

.hero__city {
  margin: 0;
  margin-bottom: 13px;
  text-align: end;
  font-family: "Alumni Sans", sans-serif;
  font-size: 32px;
  line-height: 32px;
  font-weight: 600;
}

.hero__wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 47px;
}

.hero__list {
  margin: 0;
  padding: 0;
  margin-right: 21px;
  list-style: none;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  text-align: end;
}

.hero__title {
  font-family: "Alumni Sans", sans-serif;
  font-size: 62px;
  line-height: 62px;
  font-weight: 800;
  margin: 0;
  width: 155px;
  text-align: end;
  letter-spacing: 0.01em;
}

.hero__button {
  text-decoration: none;
  color: inherit;
  display: block;
  box-sizing: border-box;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 13px;
  font-weight: 700;
  border: solid 2px #ffffff;
  padding: 22px;
}
.hero__button:focus:not(.hero__button--disabled) {
  color: #103aae;
  border-color: #000000;
  background-color: rgba(255, 255, 255, 0.8);
}
.hero__button:hover:not(.hero__button--disabled) {
  color: #103aae;
  border-color: #ffffff;
  background-color: #ffffff;
}
.hero__button:active:not(.hero__button--disabled) {
  color: #102654;
  border-color: #ffffff;
  background-color: #ffffff;
}

.hero__button--disabled {
  color: transparent;
  opacity: 0.3;
  cursor: not-allowed;
}

@media (min-width: 768px) {
  .hero {
    padding: 0;
    padding-bottom: 100px;
  }

  .hero__bg-wrapper {
    padding-right: 352px;
    padding-top: 32px;
    padding-bottom: 32px;
    background-image: url("../img/hero-img.jpg");
    background-position: right top;
    background-repeat: no-repeat;
    background-size: 324px 390px;
  }

  .hero__wrapper {
    justify-content: space-between;
    margin-bottom: 50px;
  }

  .hero__city {
    margin-bottom: 9px;
    margin-right: 2px;
