    line-height: 85px;
    width: 187px;
    letter-spacing: 2px;
  }

  .hero__list {
    padding-bottom: 25px;
    line-height: 22px;
  }

  .hero__button {
    width: 185px;
    padding: 20px;
    margin-left: auto;
