.video {
  position: relative;
  width: 320px;
  height: 170px;
  margin: 0 auto;
}

.video__img {
  display: block;
  width: 320px;
  height: 170px;
}

.video__btn {
  color: #ffffff;
  cursor: pointer;
  position: absolute;
  width: 72px;
  height: 72px;
  border-radius: 50%;
  border: 2px solid rgba(237, 2, 51, 0.3);
  background-color: transparent;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.video__btn::before {
  position: absolute;
  content: "";
  width: 52px;
  height: 52px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
}
.video__btn:focus {
  border: 3px solid #102654;
}
.video__btn:focus::before {
  background-image: none;
  background-color: #c8c9cc;
}
.video__btn:focus .video__btn-icon {
  color: #102654;
}
.video__btn:hover {
  border: 3px solid rgba(16, 38, 84, 0.6);
}
.video__btn:hover .video__btn-icon {
  color: rgba(16, 38, 84, 0.6);
}
.video__btn:hover::before {
  background-image: none;
  background-color: #ffffff;
}
.video__btn:active {
  border: 3px solid #102654;
}
.video__btn:active::before {
  background-image: none;
  background-color: #ffffff;
}
.video__btn:active .video__btn-icon {
  color: #102654;
}
.video__btn:disabled {
  border: 3px solid rgba(16, 38, 84, 0.3);
  cursor: not-allowed;
}
.video__btn:disabled::before {
  background-image: none;
  background-color: #ffffff;
}
.video__btn:disabled .video__btn-icon {
  color: rgba(16, 38, 84, 0.3);
}

.video__btn-icon {
  position: absolute;
  width: 13px;
  height: 10px;
  top: 50%;
  left: 50%;
  transform: translate(-42%, -50%);
}

@media (min-width: 768px) {
  .video {
    margin-top: -121px;
    width: 270px;
    height: 170px;
  }

  .video__img {
    width: 270px;
    height: 170px;
  }
}
@media (min-width: 1366px) {
  .video {
    margin: 0;
    margin-top: -115px;
    margin-right: auto;
    width: 360px;
    height: 230px;
  }

  .video__btn {
    width: 100px;
    height: 100px;
  }
  .video__btn::before {
    width: 72px;
    height: 72px;
  }

  .video__btn-icon {
    width: 17px;
    height: 17px;
    transform: translate(-38%, -50%);
  }

  .video__img {
    width: 360px;
    height: 230px;
  }
}
