.games {
  padding-top: 25px;
  padding-bottom: 25px;
}

.games__info-wrapper {
  display: grid;
  grid-template-columns: 117px 1fr;
  column-gap: 10px;
}

.games__text-wrapper {
  color: #ffffff;
  background-color: #ed0233;
  padding: 40px;
}

.games__title {
  font-family: "Alumni Sans", sans-serif;
  font-size: 48px;
  line-height: 54px;
  font-weight: 600;
  margin: 0;
}

.games__date-wrapper {
  margin-left: auto;
  padding-top: 2px;
}

.games__date-text {
  font-size: 16px;
  line-height: 20px;
  margin: 0;
  padding-bottom: 1px;
  border-bottom: 1px solid #ffffff;
}

.games__date {
  margin: 0;
  display: grid;
  grid-template-columns: auto 1fr;
  padding-top: 3px;
  margin-bottom: 5px;
}

.games__date-number {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  grid-row: 1/3;
  padding-right: 4px;
}

.games__date-month {
  font-size: 12px;
  line-height: 12px;
}

.games__date-year {
  font-size: 12px;
  line-height: 12px;
  grid-column: 2/3;
}

.games__date-time {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  margin: 0;
  padding-top: 1px;
}

.games__game {
  font-size: 16px;
  line-height: 20px;
}

.games__btn {
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  cursor: pointer;
  display: block;
  text-align: center;
  padding: 22px 10px;
  border: 2px solid #ffffff;
  width: 100%;
  margin: 0 auto;
}
.games__btn:focus:not(.hero__button--disabled) {
  color: #103aae;
  border-color: #000000;
  background-color: rgba(255, 255, 255, 0.8);
}
.games__btn:hover:not(.hero__button--disabled) {
  color: #103aae;
  border-color: #ffffff;
  background-color: #ffffff;
}
.games__btn:active:not(.hero__button--disabled) {
  color: #102654;
  border-color: #ffffff;
  background-color: #ffffff;
}
.games__btn:disabled {
  color: transparent;
  opacity: 0.3;
  cursor: not-allowed;
