.control {
  position: relative;
}

.control__btn {
  display: none;
  position: absolute;
  cursor: pointer;
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 1px solid transparent;
  background-color: transparent;
  top: 50%;
  transform: translateY(-50%);
}
.control__btn::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 1px;
  background-color: #102654;
  left: 12px;
  top: 18px;
  transform: rotate(-45deg);
}
.control__btn::after {
  position: absolute;
  content: "";
  width: 23px;
  height: 1px;
  background-color: #102654;
  top: 34px;
  left: 12px;
  transform: rotate(45deg);
}
.control__btn:focus:not(:disabled) {
  border: 1px solid #102654;
  background-color: #e8e8e8;
}
.control__btn:hover:not(:disabled) {
  border: 1px solid #102654;
}
.control__btn:active:not(:disabled) {
  border: 1px solid #102654;
  background-color: #102654;
}
.control__btn:active:not(:disabled)::after {
  background-color: #ffffff;
}
.control__btn:active:not(:disabled)::before {
  background-color: #ffffff;
}
.control__btn:disabled {
  border: 1px solid transparent;
  opacity: 0.3;
}

.control__btn--end {
  transform: translateY(-50%) rotate(180deg);
  left: auto;
}

@media (min-width: 768px) {
  .control__btn {
    display: block;
  }

